<template>
  <div class="objective padding1" id="objective">
    <!-- 培养目标 -->
    <div class="objective-dv0">
      <crumb v-if="isOperate ==='taskSituation'" :data="pageTitle"></crumb>
      <crumb v-else></crumb>
    </div>
    <div v-if="roleName === '学生'">
      <div class="lp">
        <h3>{{datalist.target == null ?'' :datalist.target.name}}培养目标</h3>
        <div  v-html="datalist.target==null ? '' : datalist.target.content" id="objectiveTarget"></div>
      </div>
      <ul>
        <li class="targetItem" v-for="(items, index) in datalist.list" :key="index" :title="items.content">
            <div class="index">
              <img v-if="(index + 1) % 4 === 1 || (index + 1) % 4 === 2" src="@/assets/img/bianhao.png" alt="">
              <img v-if="(index + 1) % 4 === 3 || (index + 1) % 4 === 0" src="@/assets/img/bianhao2.png" alt="">
              <span>{{items.code}}</span>
            </div>
            <div class="itemContent">{{items.content}}</div>
        </li>
      </ul>
    </div>
    <div v-else-if="roleName === '课程负责人' || roleName === '教师'">
      <div class="lp">
        <h3>{{datalists.target == null ?'' :datalists.target.name}}培养目标</h3>
        <div v-html="datalists.target==null ? '' : datalists.target.content" id="objectiveTarget"></div>
      </div>
      <ul>
        <li class="targetItem" v-for="(items, index) in datalists.list" :key="index" :title="items.content">
            <div class="index">
              <img v-if="(index + 1) % 4 === 1 || (index + 1) % 4 === 2" src="@/assets/img/bianhao.png" alt="">
              <img v-if="(index + 1) % 4 === 3 || (index + 1) % 4 === 0" src="@/assets/img/bianhao2.png" alt="">
              <span>{{items.code}}</span>
            </div>
            <div class="itemContent">{{items.content}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pageTitle:['培养方案内容', '培养目标'],
      isOperate:this.$route.query.type, //教学任务管理-任务完成情况跳转过来携带 type:taskSituation无操作权限
      datalist: {},
      datalists: {},
      versionList: [],
      versionId: 0,
      roleName: ""
    };
  },
  methods: {
    ///information/information/getTrainTarget content
    getTrainTarget() {
      this.$get("/info/information/getTrainTarget").then(res => {
        this.datalist = res;
       
      });
    },
    getClaim(id) {
      this.versionId = id;
      this.$get("/info/teacherinfo/getTrainTargetByVersionId", {
        versionId: this.versionId
      }).then(res => {
      
        this.datalists = res;
      });
    },
    // getAllVersion(){
    //   this.$get('/info/teacherinfo/getAllVersion').then(res=>{
    //     console.log(res,'版本')
    //       this.versionList = res.versions
    //       if(this.versionList.length > 0) {
    //         this.getClaim(this.versionList[0].id)
    //       }
    //   }).catch(err => {
    //     this.notify({
    //       title: 'error',
    //       message: '系统繁忙，请刷新重试!',
    //       type: 'err',
    //       duration: '1500',
    //     });

    //   })
    // },
    getInfo() {
      if (localStorage.getItem("versionId")) {
        this.getClaim(localStorage.getItem("versionId"));
      } else {
        setTimeout(() => {
          this.getInfo();
        }, 200);
      }
    }
  },

  created() {
    this.roleName = localStorage.getItem("roleName");
    
  },
  mounted() {
    if (
      localStorage.getItem("roleName") === "课程负责人" ||
      localStorage.getItem("roleName") === "教师"
    ) {
      this.getInfo();
    } else if (localStorage.getItem("roleName") === "学生") {
      this.getTrainTarget();
    }
  }
};
</script>
<style lang="scss">
#objective{
  margin-bottom: 87px;
  #objectiveTarget{
    padding-top: 10px; 
    padding-bottom: 20px; 
    line-height: 2;
      p{
         color:#323232 !important;
         font-size:14px !important;
         font-weight:normal !important;
        span{
          color:#323232 !important;
          font-size:14px !important;
          font-weight:normal !important;
        }
      }
    }
}  
</style>
<style lang="scss" scoped>
.objective {
  background-color: #f9f9f9;
  text-align: left;
  .lp {
    background-color: #fff;
    padding: 30px;
    margin-bottom: 20px;
    border-radius: 10px;
    >h3 {
      font-size:20px;
      color: #323232;
      font-weight:bold;
      margin-bottom: 30px;
    }
  }
  ul {
    overflow: hidden;
    .targetItem {
      box-sizing: border-box;
      padding: 38px 30px;
      float: left;
      width: 49%;
      overflow: hidden;
      margin-bottom: 20px;
      background-color: #fff;
      border-radius: 10px;
      height: 132px;
      &:nth-of-type(even) {
        margin-left: 2%;
      }
      .index {
        width: 44px;
        height: 44px;
        text-align: center;
        line-height: 44px;
        float: left;
        position: relative;
        span {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%)
        }
      }
      .itemContent {
        margin-left: 65px;
        line-height: 2;
        color: #323232;
        overflow:hidden;
        font-size:14px;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:2;//控制行数
        font-weight: 700;
      }
    }
  }
}
</style>




